import { useEffect, useState } from "react";

export default function(min: number = 0, max: number, defaultValue? : number) {
    let [value, setValue] = useState(defaultValue || min)

    useEffect(()=>{
        if(value < min) {
            setValue(min)
        } else if (value > max) {
            setValue(max)
        }
        
        if (!value && value != 0) {
            setValue(min)
        }
    },[value])
    
    
    return {
        value,
        onChange : (e: number) => {
            setValue(e)
        }
    }
}