import { createSlice } from "@reduxjs/toolkit"
import { PayloadAction } from "@reduxjs/toolkit"
import { ITask } from "../../api/models/Task"
import { RootState } from "../store"

interface IApp {
    mode : "Work" | "Break" | "Prepare" | undefined,
    menu : boolean,
    history : Array<ITask[]>,
    historyStep : number
 }
 
 const initialState: IApp = {
    mode : "Prepare",
    menu : false,
    history : [],
    historyStep : 0
 }

const AppState = createSlice({
    name : "app",
    initialState,
    reducers : {
      setMode(state, payload: PayloadAction<IApp['mode']>) {
         state.mode = payload.payload
      },

      setMenu(state, payload: PayloadAction<boolean>) {
         state.menu = payload.payload
      },

      setHistory (state, {payload}: PayloadAction<ITask[][]>) {
         state.history = payload
      },

      setHistoryStep (state, {payload}: PayloadAction<number>) {
         state.historyStep = payload
      }
    }
 })

export const AppReducer = AppState.reducer
export const AppActions = AppState.actions
export const AppSelector = (state : RootState) => state.app