import useSelection from 'antd/lib/table/hooks/useSelection';
import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { PlanActions } from '../../redux/slices/plan';
import { PlansMenuSelector } from '../../redux/slices/plans';
import { RootState } from '../../redux/store';

interface Props {}

function PlansList(props: Props) {
    const {} = props

    const plansMenu = useSelector(PlansMenuSelector)
    const plan = useSelector((state : RootState) => state.plan).data
    const { setPlan } = PlanActions
    const dispatch = useDispatch()

    return <div className="planner__list">
    {plansMenu.allPlans.map((item) => {
      return (
        <div
          className={
            "plan" + (item.id == plan?.id ? " " + "current" : "")
          }
          key={item.id}
          onClick={() => dispatch(setPlan(item))}
        >
          <div className="plan__name">{item.name}</div>
          <div className="plan__date">{item.date}</div>
        </div>
      );
    })}
  </div>
}

export default PlansList
