import React, { useEffect, useRef, useState } from "react";
import "./styles/main.scss";
import { Button, Input, InputNumber, Calendar, List } from "antd";
import useInput from "./hooks/useInput";
import Task, { SortableList } from "./components/Task/Task";
import useRange from "./hooks/useRange";
import { ITask } from "./api/models/Task";
import toFormat from "./helpers/toFormat";
import useTimer from "./hooks/useTimer";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "./redux/store";
import { PlansMenuActions } from "./redux/slices/plans";
import { AppActions, AppSelector } from "./redux/slices/app";
import { PlanActions } from "./redux/slices/plan";
import api from "./api/api";
import type { Moment } from "moment";
import { IPlan } from "./api/models/Plan";
import todaysDate from "./helpers/todaysDate";
import { MenuOutlined, 
         LoadingOutlined, 
         ArrowLeftOutlined, 
         ArrowRightOutlined } from "@ant-design/icons";
import MobileDetect from "mobile-detect";
import {draft, IDraft} from "./redux/slices/plan"
import useDrag from "./hooks/useDrag";
import useHasChanged from "./hooks/useHasChanged";
import TimerController from "./components/TimerController/TimerController";
import AppHeader from "./components/AppHeader/AppHeader";
import MainHeader from "./components/AppHeaders/MainHeader/MainHeader";
import AsideHeader from "./components/AppHeaders/AsideHeader/AsideHeader";
import PlansList from "./components/PlansList/PlansList";
import {arrayMoveImmutable} from "array-move"


function App() {
  const dispatch = useDispatch<any>();

  const app = useSelector((state: RootState) => state.app);
  const plansMenu = useSelector((state: RootState) => state.plansMenu);
  const plan = (useSelector((state: RootState) => state.plan)).data;

  const history = useSelector(AppSelector).history
  const historyChanged = useHasChanged(history)
  const historyStep = useSelector(AppSelector).historyStep


  const { setPlans, addPlan, deletePlan } = PlansMenuActions;
  const { setMode, setHistoryStep, setMenu, setHistory } = AppActions;
  const { setTasks, addTask, setPlan, setName, deleteTask } = PlanActions;

  let [currentTask, setCurrentTask] = useState<null | number>(null);
  
  let el = document.querySelector(".planner__wrapper")

  let [input, setValue] = useInput("");

  // 

  let [work_mins, setWorkMins] = useState(10);
  let [work_secs, setWorkSecs] = useState(0);
  let [break_mins, setBreakMins ]= useState(2);
  let [break_secs, setBreakSecs] = useState(0);

  //    

  let [total, setTotal] = useState(0);
  let [startTaskTimer, toggleTaskTimer] = useTimer();
  let [time, setTime] = useState(0);
  let [date, setDate] = useState(todaysDate());

  //   

  const md = new MobileDetect(window.navigator.userAgent);
  const isMobile = md.mobile() != null;
  const menu = useSelector(AppSelector).menu

  // Drag and Drop

  const [dragStart, onDrop] = useDrag(plan?.tasks, (items) => {
    updateHistory(items)
    dispatch(setTasks(items))
  })

  //  -

  function addTaskWrapper(item: string): void {
    if (item) {
      
      if (
        (work_mins || work_secs) &&
        (break_mins || break_secs)
      ) {
        let task: ITask = {
          name: item,
          work_time: work_mins * 60 + work_secs,
          break_time: break_mins * 60 + break_secs,
          id: Math.random(),
          completed: false,
        };

        let updatedTasks = [...plan.tasks, task]

        dispatch(setTasks(updatedTasks));
        updateHistory(updatedTasks)
        // setHistory([...history, [...plan.tasks, task]])
        setValue("")
      }
    } else {
      alert("Entry all fields");
    }
  }

  const removeTask = function(taskId : number){
    if (app.mode == "Prepare") {
      let conf = confirm(`Delete this task?`);
      if (conf) {
        let updatedTasks = plan.tasks.filter(task => task.id != taskId)
        dispatch(setTasks(updatedTasks))
        // updateHistory(updatedTasks)
      }
    } else {
      alert("Cannot delete task while work");
    }
}

  function deletePlanWrapper(id : number){
    let c = confirm("Delete plan?")

    if (c) {
      api.client.deletePlan(id)
      .then(deleted => {
        dispatch(setPlans(plansMenu.allPlans.filter((item)=>{
          return item.id != deleted!.id
        })))
      })
      dispatch(setPlan(plansMenu.allPlans[0]))
    }
  }

  function onSelect (value: Moment) {
    setDate(value.format("YYYY-MM-DD"));
  };

  function updateHistory (newTasks : ITask[]) {
    if (plan.tasks.length) {
      let newHistory = history.slice(0, historyStep + 1)
      let updatedHistory = [...newHistory, newTasks]
      dispatch(setHistory(updatedHistory))
      dispatch(setHistoryStep(updatedHistory.length - 1))
    }
  }

  function onSortEnd({oldIndex, newIndex} : any) {
    let updatedTasks = arrayMoveImmutable(plan.tasks, oldIndex, newIndex)
      dispatch(setTasks(updatedTasks))
      updateHistory(updatedTasks)
  }

  //  -

  async function startTimer(delay: number) {
    setTime(delay);
    let res = startTaskTimer((time: number) => {
      setTime(time);
    }, delay);
    return res;
  }

  async function run () {
    if (plan) {
      if (app.mode == "Prepare") {
        for (let i = 0; i < plan.tasks.length; i++) {
          let item = plan.tasks[i];
          if (item) {
            if (!item.completed) {
              dispatch(setMode("Work"));
              setCurrentTask(item.id);
              await startTimer(item.work_time);

              dispatch(setMode("Break"));
              await startTimer(item.break_time);

            } else {
              continue;
            }
          } else {
            break;
          }
        }
        setCurrentTask(null);
        dispatch(setMode("Prepare"));
      }
    }
  };

  async function savePlan () {
    let new_plan: IDraft | IPlan = JSON.parse(JSON.stringify(plan))
    if (!new_plan.isDraft) {
      let overwrite = confirm("Do you want overwrite plan?");
      if (overwrite) {
        delete new_plan.isDraft
        let res = await api.client.updatePlan(plan.id, new_plan);
        let newData = plansMenu.allPlans.map((item: IPlan) => {
          if (item.id == res.id) {
            return res;
          }
          return item;
        });

        dispatch(setPlans(newData));
      }
    } else {
      new_plan.id = Math.random();

      if (new_plan.name) {
        if (new_plan.tasks.length) {
          delete new_plan.isDraft
          let res = await api.client.addPlan(new_plan);
          dispatch(addPlan(res));
          dispatch(setPlan(res));
        } else {
          alert("Cannot save plan without tasks");
        }
      } else {
        alert("Please, entry a plan name");
      }
    }
  };

  // useEffect'

  useEffect(() => {
    api.client.getAll().then((res: IPlan[]) => {
      res = res.sort((a, b) => {
        return +a.date.split("-").join("") - +b.date.split("-").join("");
      });
      dispatch(setPlans(res));
      if (res.length) {
        dispatch(setPlan(res[0]));
      }
    });
  }, []);

  useEffect(() => {
    let keydown = (e: any) => {
      if (e.code == "Enter") {
        addTaskWrapper(input.value);
      }
    };

    document.addEventListener("keydown", keydown);
    return () => document.removeEventListener("keydown", keydown);
  }, [input.value]);

  useEffect(() => {
    if (plan) {
      let sum = plan.tasks.reduce((a, b) => a + b.work_time + b.break_time, 0);
      setTotal(sum);
      el?.scrollTo(0, el.scrollHeight)
      
    }
  }, [plan?.tasks]);

  useEffect(() => {
    if (plan.id) {
      dispatch(setHistory([plan.tasks]))
    }
  }, [plan.id])

  useEffect(() => {
    if (history.length) {
      dispatch(setTasks(history[historyStep]))
    }
  }, [historyStep])

  return (
    <div className="App">
      <div className="planner">
        <div className="planner__col main">
          <div className="group">

            <MainHeader />

            <div className="planner__wrapper"
            onTouchEnd={(e) => console.log(e)}
            >
              {/* <List
                size="small"
                bordered
                dataSource={plan?.tasks || []}
                renderItem={(item, i) => {
                  return (
                    <Task
                      {...item}
                      isCurrent={item.id === currentTask}
                      dragStart={(e) => dragStart(e, item)}
                      onDrop={(e) => onDrop(e, item)}
                      onClick={removeTask}
                    />
                  );
                }}
                className="planner__list"
              /> */}
              <SortableList items={plan.tasks} 
              onSortEnd={onSortEnd} 
              pressDelay={200}
              onElementClick={removeTask}/>
            </div>
          </div>
          <div className="planner__footer">
            <div className="planner__new-task">
              <Input {...input} placeholder="Enter task" type="text" />
              <Button onClick={() => addTaskWrapper(input.value)} type="primary">
                Add Task
              </Button>
            </div>
            <div className="planner__row">
              <TimerController 
              title="Work Time"
              onChange={(time) => {
                setWorkMins(time[0])
                setWorkSecs(time[1])
              }}/>

              <TimerController 
              title="Break Time"
              onChange={(time) => {
                setBreakMins(time[0])
                setBreakSecs(time[1])
              }}/>
              
            </div>
          </div>
          <div className="planner__row">
            <Button
              type="primary"
              disabled={app.mode != "Prepare"}
              onClick={() => setTasks([])}
            >
              Reset
            </Button>

            {total ? <div>{"Total " + toFormat(total)}</div> : ""}
            <Button
              type="primary"
              onClick={() => run()}
              disabled={!plan || plan?.tasks?.length == 0}
            >
              {app.mode == "Prepare" ? "Run" : <LoadingOutlined />}
            </Button>
          </div>

          {time ? (
            <div className="timer">
              <Button onClick={() => toggleTaskTimer()} type="primary">
                Stop/Start
              </Button>
              <div className="timer__value">
                {`${app.mode} ${toFormat(time)}`}
              </div>
            </div>
          ) : (
            ""
          )}

          <Calendar
            className="calendar"
            fullscreen={false}
            onSelect={onSelect}
            headerRender={() => {
              return <div className="calendar__header">{date}</div>;
            }}
          />

          <div className={"planner__row footer" + (!plan && ` text-end`)}>
            {plan ? (
              <Button
                className="planner__btn"
                onClick={() => deletePlanWrapper(plan?.id || 0)}
                disabled={app.mode != "Prepare"}
                type="primary"
              >
                Delete Plan
              </Button>
            ) : (
              ""
            )}

            {plan ? (
              <Button
                type="primary"
                onClick={() =>
                dispatch(setPlan(draft))}
              >
                New Plan
              </Button>
            ) : (
              ""
            )}

            <Button
              className="planner__btn save-btn"
              onClick={() => savePlan()}
              type="primary"
            >
              Save Plan
            </Button>
          </div>
        </div>

        <div
          className="planner__col aside"
          style={{ visibility: (menu || !isMobile) ? "visible" : "hidden" }}
        >
          <AsideHeader />
          <PlansList />
        </div>
      </div>
    </div>
  );
}

export default App;
