import React, { DragEventHandler, useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import toFormat from "../../helpers/toFormat";
import { RootState } from "../../redux/store";
import { ITask } from "../../api/models/Task";
import addClass from "../../helpers/addClass";
import { PlanActions } from "../../redux/slices/plan";
import "./Task.scss"

import {SortableContainer, SortableElement} from 'react-sortable-hoc';

interface Props extends ITask {
  isCurrent?: boolean;
  dragStart?: DragEventHandler;
  onDrop?: DragEventHandler;
  onClick?: Function
}

interface SortableProps {
  items : ITask[],
  onElementClick? : Function
}

const SortableTask = SortableElement<Props>((value: Props) => <Task {...value}/>)

export const SortableList = SortableContainer<SortableProps>((props : SortableProps) => {
  console.log(props)
  return <div className="planner__list">
    {
      props.items.map((item : Props, i : number) => {
        return <SortableTask key={`item-${i}`} index={i} {...item}
        onClick={props.onElementClick}
        />
      })
    }
  </div>
})

function Task(props: Props) {
  const dispatch = useDispatch()
  const id = props.id
  const { deleteTask } = PlanActions

  const cloneRef = useRef<any>()
  const elRef = useRef<any>()
  let {
    name,
    break_time,
    work_time,
    isCurrent = false,
    completed,
  } = props;

  let { dragStart, onDrop } = props;
  let app = useSelector((state: RootState) => state.app);
  
  let time_template = `${toFormat(work_time)} +${toFormat(break_time)}`;

  function dragEnter(e: any) {
    e.target.style.background = "lightgray";
  }

  function dragLeave(e: any) {
    e.target.style.background = "white";
  }

  function dragEnd(e: any) {
    e.target.style.background = "white";
  }

  function prevent(e: any) {
    e.preventDefault();
  }

  function onClick() {
    if (props.onClick) {
      props.onClick(id)
    }
  }

  return (
    <div
      className={
        `task` +
        addClass(isCurrent, "current") +
        addClass(app.mode == "Break", "break") +
        addClass(completed, "completed")
      }
      onClick={() => onClick()}
    >
      <div className="task__name">{name}</div>
      <div className="task__duration">
        <div className="task__time">{time_template}</div>
      </div>
    </div>
  );
}

export default Task;
