import { IApi } from "../api"
import { IPlan } from "../models/Plan"
import { ITask } from "../models/Task"


class LocalStorage implements IApi {
    async getAll() {
        let res = localStorage.getItem('plans')
        let data: IPlan[]
        if(res) {
            data = JSON.parse(res)
        } else {
            data = []
        }
        return data
    }

    async getPlan(plan_id : number) {
        let data = await this.getAll()
        let plan = data.find(item => item.id == plan_id)
        return plan
    }

    async addPlan(plan: IPlan) {
        let data = await this.getAll()
        data.push(plan)
        localStorage.setItem('plans',JSON.stringify(data))
        return plan
    }

    private sync(plans : IPlan[]){
        localStorage.setItem("plans",JSON.stringify(plans))
    }

    async updatePlan(plan_id: number, data : IPlan) {
        let plans = await this.getAll()
        let index = plans.findIndex(item => item.id == plan_id)

        plans[index] = data
        this.sync(plans)
        return plans[index]
    }

    async deletePlan(id: number) {
        let plans = await this.getAll()
        let plan: IPlan | undefined = plans.find((item: IPlan) => item.id == id)

        if(plan) {
            plans = plans.filter((item: IPlan) => item.id != plan!.id)
        }

        this.sync(plans)
        return plan
    }
}

export default LocalStorage