import { useState, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux";
import { ITask } from "../api/models/Task";
import { PlanActions } from "../redux/slices/plan";
import { RootState } from "../redux/store";


export default function(items : any[], callback : (data : any[]) => void) {
    const [dragTask, setDragTask] = useState<ITask | null>(null)

    const dragStart = useCallback((e: any, task: ITask) => {
        setDragTask(task);
      }, []);
    
    const onDrop = useCallback((e: any, task: ITask) => {
    e.preventDefault();
        e.target.style.background = "white";
        let index = items.findIndex((item) => item.id! == task.id);
        let dragIndex = items.findIndex(
        (item) => item.id == dragTask?.id
        );
        if (dragTask) {
        let newData : any[] = JSON.parse(JSON.stringify(items));
        newData.splice(dragIndex, 1);
        newData.splice(index, 0, dragTask!);        
        setDragTask(null)
        callback(newData)
        }
    }, [items, dragTask]);

    return [dragStart, onDrop]
}