import { ArrowLeftOutlined, ArrowRightOutlined } from '@ant-design/icons'
import { Button, Input } from 'antd'
import React, { useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { AppActions, AppSelector } from '../../../redux/slices/app'
import { PlanActions } from '../../../redux/slices/plan'
import { RootState } from '../../../redux/store'
import AppHeader from '../../AppHeader/AppHeader'

interface Props {}

function MainHeader(props: Props) {
    const {} = props

    const {setName} = PlanActions
    const { setHistoryStep } = AppActions

    const dispatch = useDispatch()
    const historyStep = useSelector(AppSelector).historyStep
    const history = useSelector(AppSelector).history
    const plan = useSelector((state : RootState) => state.plan).data

    useEffect(() => {
        console.log(history)
    }, [history])

    useEffect(() => {
        console.log(historyStep)
    }, [historyStep])

    return <AppHeader className="main">
    <Input
        className="planner__plan-name"
        value={plan?.name}
        onChange={(e) => dispatch(setName(e.target.value))}
        placeholder="Plan Name"
      ></Input>

      <div className="planner__plan-buttons">
        <Button
        disabled={historyStep <= 0}
        onClick={() => dispatch(setHistoryStep(historyStep - 1))}
        >
          <ArrowLeftOutlined />
        </Button>
        <Button
        disabled={historyStep + 1 >= history.length}
        onClick={() => dispatch(setHistoryStep(historyStep + 1))}
        >
        <ArrowRightOutlined />
        </Button>
      </div>
    </AppHeader>
}

export default MainHeader
