import { createSlice, isDraft, PayloadAction } from "@reduxjs/toolkit";
import { IPlan } from "../../api/models/Plan";
import { ITask } from "../../api/models/Task";
import todaysDate from "../../helpers/todaysDate"

export interface IDraft extends IPlan {
    isDraft : true
  }

interface InitState {
    data : IPlan | IDraft
}

export let draft : IDraft = {
    name : "",
    id : Math.random(),
    date : todaysDate(),
    tasks : [],
    isDraft : true
}

const initialState: InitState = {
    data : draft
};

const PlanState = createSlice({
    name : "plan",
    initialState,
    reducers : {
        setName(state, {payload}: PayloadAction<string>) {
            state.data!.name = payload
        },

        setPlan(state, payload: PayloadAction<IPlan | IDraft>) {
            state.data = payload.payload
        },

        setTasks(state, payload: PayloadAction<ITask[]>) {
            if (state.data) {
                state.data.tasks = payload.payload
            }
        },

        addTask(state, {payload} : PayloadAction<ITask>) {
            if (state.data) {
                state.data.tasks.push(payload)
            }
        },

        deleteTask(state, {payload}: PayloadAction<number>) {
            if (state.data) {
                state.data.tasks = state.data?.tasks.filter(task => {
                    return task.id != payload
                })
            }
        }
    }
})

export const PlanReducer = PlanState.reducer
export const PlanActions = PlanState.actions
