import LocalStorage from "./clients/LocalStorage"
import { IPlan } from "./models/Plan"
import { ITask } from "./models/Task"
type clients = LocalStorage
export interface IApi {
    getAll : () => Promise<IPlan[]>,
    getPlan : (id: number) => Promise<IPlan | undefined>,
    addPlan : (plan: IPlan) => Promise<IPlan>,
    deletePlan : (id: number) => Promise<IPlan | undefined>,
    updatePlan : (id : number, data: IPlan) => Promise<IPlan>
}

class API {

    client: IApi
    constructor(client : IApi) {
        this.client = client
    }
}

let api = new API(new LocalStorage())

export default api