import { InputNumber } from 'antd'
import { AnyMxRecord } from 'dns'
import React, { useEffect } from 'react'
import useInput from '../../hooks/useInput'
import useRange from '../../hooks/useRange'
import "./TimerController.scss"

interface Props {
    onChange : (time : number[]) => any,
    title : string
}

function TimerController(props: Props) {
    const {} = props

    const mins = useRange(0, 59, 10)
    const secs = useRange(0, 59)

    useEffect(() => {
        props.onChange([mins.value, secs.value])
    }, [mins.value, secs.value])

    return <div className="timer">
    <div className="timer__title">{props.title}</div>
    <div className="timer__inputs">
      <InputNumber
        {...mins}
        controls={false}
        placeholder="Min"
        className="timer__input"
      />
      :
      <InputNumber
        {...secs}
        controls={false}
        placeholder="Sec"
        className="timer__input"
      />
    </div>
  </div>
}

export default TimerController
